import React, { useState, useEffect } from "react";

import {
    Dialog, DialogTitle, DialogContent,
    DialogContentText, DialogActions,
    Button, withMobileDialog, 
    makeStyles, Grid, FormLabel,
    FormControlLabel, RadioGroup, Radio, capitalize
} from "@material-ui/core";

import { MuiPickersUtilsProvider, KeyboardDateTimePicker } from "@material-ui/pickers";
import MomentUtils from "@date-io/moment";
import moment from "moment";
import { useSnackbar } from "notistack";

import { generateAlertFyiMessage, getAlertFyiTriggers } from "../ajax";
import { SlideTransition, FadeTransition } from "../../reusables/TransitionList";
import DynaslopeSiteSelectInputForm from "../../reusables/DynaslopeSiteSelectInputForm";

const useStyles = makeStyles(theme => ({
    inputGridContainer: {
        marginTop: 6,
        marginBottom: 6
    },
    checkboxGridContainer: {
        marginTop: 12,
        marginBottom: 6
    },
    selectInput: {
        width: "auto",
        [theme.breakpoints.down("xs")]: {
            width: "250px"
        }
    },
    root: {
        width: "90%",
    },
    backButton: {
        marginRight: 1
    },
    instructions: {
        marginTop: 1,
        marginBottom: 1,
    },
    link: { textDecoration: "none" }
}));

function AlertFyiTemplateModal (props) {
    const {
        fullScreen, isOpen,
        clickHandler, setComposedMessage
    } = props;

    const classes = useStyles();

    const [date_time, setDateTime] = useState(moment());
    const [site_list, setSiteList] = useState(null);
    const [action, setAction] = useState(null);
    const [internal_sym_id, setInternalSymId] = useState(null);

    const [trigger_list, setTriggerList] = useState([]);
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    
    const update_datetime = value => setDateTime(value);
    const update_site_value = value => setSiteList(value);

    useEffect(() => {
        getAlertFyiTriggers(data => setTriggerList(data));
    }, []);

    const snackBarActionFn = key => (<Button
        color="primary"
        onClick={() => { closeSnackbar(key); }}
    >
        Dismiss
    </Button>);

    const [is_disabled, setIsDisabled] = useState(true);
    useEffect(() => {
        setIsDisabled(date_time === null || site_list === null ||
            action === null || internal_sym_id === null);
    }, [date_time, site_list, action, internal_sym_id]);

    const generateTemplate = () => {
        const payload = {
            ts: date_time.format("YYYY-MM-DD HH:mm:00"),
            sites: site_list.map(x => x.value),
            action,
            internal_sym_id: parseInt(internal_sym_id, 10)
        };

        generateAlertFyiMessage(payload, data => {
            setComposedMessage(data);
            clickHandler();
            enqueueSnackbar(
                "Alert FYI message successfully generated!",
                { variant: "success", autoHideDuration: 3000, action: snackBarActionFn }
            );
        }, error => {
            enqueueSnackbar(
                error.response.data,
                { variant: "error", action: snackBarActionFn }
            );
        });
    };

    return (
        <Dialog
            fullWidth
            fullScreen={fullScreen}
            open={isOpen}
            aria-labelledby="form-dialog-title"
            TransitionComponent={fullScreen ? SlideTransition : FadeTransition}      
        >
            <DialogTitle id="form-dialog-title">
                Generate alert FYI message
            </DialogTitle>
            <DialogContent>
                <DialogContentText>
                    Fill-up the form with needed data.
                </DialogContentText>

                <Grid
                    container 
                    spacing={1} 
                    alignItems="center"
                    justify="space-evenly"
                >
                    <Grid item xs={12} className={classes.inputGridContainer}>
                        <DynaslopeSiteSelectInputForm 
                            value={site_list}
                            changeHandler={update_site_value}
                            isMulti                    
                        />
                    </Grid>

                    <Grid item xs={12} sm={6} className={classes.inputGridContainer}>
                        <MuiPickersUtilsProvider utils={MomentUtils}>
                            <KeyboardDateTimePicker
                                required
                                autoOk
                                label="Timestamp"
                                value={date_time}
                                onChange={update_datetime}
                                ampm={false}
                                placeholder="2010/01/01 00:00"
                                format="YYYY/MM/DD HH:mm"
                                mask="____/__/__ __:__"
                                clearable
                                disableFuture
                            />
                        </MuiPickersUtilsProvider>
                    </Grid>

                    <Grid item xs={12} sm={6} className={classes.inputGridContainer}>
                        <FormLabel component="legend" style={{ textAlign: "center" }}>
                            Alert action
                        </FormLabel>
                        <RadioGroup
                            row aria-label="position" name="position"
                            style={{ justifyContent: "space-evenly" }}
                            value={action} onChange={x => setAction(x.target.value)}
                        >
                            <FormControlLabel
                                value="raised"
                                control={<Radio color="primary" />}
                                label="Raising"
                                labelPlacement="end"
                            />
                            <FormControlLabel
                                value="lowered"
                                control={<Radio color="primary" />}
                                label="Lowering"
                                labelPlacement="end"
                            />
                        </RadioGroup>
                    </Grid>

                    <Grid item xs={12} className={classes.inputGridContainer}>
                        <FormLabel component="legend">
                            { action === "lowered"
                                ? "Highest alert trigger"
                                : "Alert trigger"
                            }
                        </FormLabel>
                        <RadioGroup
                            row aria-label="position" name="position"
                            value={internal_sym_id} onChange={x => setInternalSymId(x.target.value)}
                        >
                            {
                                trigger_list.map(x => {
                                    const { internal_sym_id: isd,
                                        internal_sym: { trigger_symbol: {
                                            alert_symbol, trigger_hierarchy: { trigger_source }
                                        }
                                        } } = x;

                                    return <FormControlLabel
                                        key={alert_symbol}
                                        value={isd.toString()}
                                        control={<Radio color="primary" />}
                                        label={`${capitalize(trigger_source)} (${alert_symbol})`}
                                        labelPlacement="end"
                                    />;
                                })
                            }
                        </RadioGroup>
                    </Grid>
                </Grid>
            </DialogContent>
               
            <DialogActions>
                <Button color="primary" onClick={generateTemplate} disabled={is_disabled}>
                    Generate
                </Button>
                <Button onClick={clickHandler}>
                    Cancel
                </Button>
            </DialogActions>
        </Dialog>
    );
}

export default withMobileDialog()(AlertFyiTemplateModal);
