import React, {
    createContext, useState, useEffect
} from "react";

import { useSnackbar } from "notistack";

import { 
    subscribeToWebSocket, unsubscribeToWebSocket,
    receiveGeneratedAlerts, receiveCandidateAlerts,
    receiveAlertsFromDB, receiveEWIInsertResponse,
    receiveAllSiteRainfallData, receiveIssuesAndReminders
} from "../../websocket/monitoring_ws";

import {
    subscribeToWebSocket as subscribeToCommsWebSocket,
    unsubscribeToWebSocket as unsubscribeToCommsWebSocket,
    receiveLatestMessages, receiveAllMobileNumbers,
    receiveAllContacts
} from "../../websocket/communications_ws";

export const WebsocketContext = createContext();

export const WebsocketProvider = ({ children }) => {
    const [generatedAlerts, setGeneratedAlerts] = useState(null);
    const [candidateAlertsData, setCandidateAlertsData] = useState(null);
    const [alertsFromDbData, setAlertsFromDbData] = useState(null);

    const [issuesAndReminders, setIssuesAndReminders] = useState([]);

    const [rainfallSummaryData, setRainfallSummaryData] = useState(null);

    const [latestMessageCollection, setLatestMessageCollection] = useState({
        inbox: null,
        unsent: null,
        queue: null
    });
    const [recipientsList, setRecipientsList] = useState([]);
    const [contacts, setContacts] = useState([]);

    const [is_reconnecting, setIsReconnecting] = useState(null);
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    const [key, setKey] = useState(null);
    useEffect(() => {
        if (is_reconnecting !== null) {
            if (is_reconnecting === true) {
                const h_key = enqueueSnackbar(
                    "Connection lost... Features will not work properly... Reconnecting...",
                    {
                        variant: "warning",
                        persist: true
                        // autoHideDuration: 7000,
                        // action: snackBarActionFn
                    }
                );
                setKey(h_key);
            } else {
                closeSnackbar(key);
                enqueueSnackbar(
                    "Reconnection successful...",
                    {
                        variant: "success",
                        autoHideDuration: 5000,
                    // action: snackBarActionFn
                    }
                );
            }
        }
    }, [is_reconnecting]);

    useEffect(() => {
        subscribeToWebSocket(setIsReconnecting);
        subscribeToCommsWebSocket(setIsReconnecting);

        receiveGeneratedAlerts(generated_alerts => setGeneratedAlerts(generated_alerts));
        receiveCandidateAlerts(candidate_alerts => setCandidateAlertsData(candidate_alerts));
        receiveAlertsFromDB(alerts_from_db => setAlertsFromDbData(alerts_from_db));
        
        receiveEWIInsertResponse(response => {
            const { snackbar_key, message, status } = response;
            closeSnackbar(snackbar_key);
            enqueueSnackbar(
                message,
                {
                    variant: status ? "success" : "error",
                    autoHideDuration: 7000
                }
            );
        });

        receiveIssuesAndReminders(data => setIssuesAndReminders(data));
        receiveAllSiteRainfallData(data => setRainfallSummaryData(data));

        receiveLatestMessages(data => setLatestMessageCollection(data));
        receiveAllMobileNumbers(data => setRecipientsList(data));
        receiveAllContacts(data => setContacts(data));

        return function cleanup () {
            unsubscribeToWebSocket();
            unsubscribeToCommsWebSocket();
        };
    }, []);

    const return_obj = {
        generatedAlerts, candidateAlertsData, alertsFromDbData,
        issuesAndReminders, rainfallSummaryData,

        latestMessageCollection, recipientsList, contacts
    };

    return (
        <WebsocketContext.Provider value={return_obj}>
            {children}
        </WebsocketContext.Provider>
    );
};