import React, { 
    Fragment, useState, useEffect,
    useContext
} from "react";

import {
    Dialog, DialogTitle, DialogContent,
    DialogActions, Button, withMobileDialog,
    Typography, Grid, DialogContentText
} from "@material-ui/core";

import moment from "moment";

import { WebsocketContext } from "../../contexts/WebsocketContext";
import NotificationSoundFolder from "../../../audio/notif_sound.mp3";


const NewAlertsModal = props => {
    const { candidateAlertsData } = useContext(WebsocketContext);

    const [isOpen, setNewAlertModalOpen] = useState(false);
    const [newAlerts, setNewAlerts] = useState([]);
    const [running_interval, setRunningInterval] = useState(null);
    const [running_timeout, setRunningTimeout] = useState(null);

    const [is_peace_mode, setIsPeaceMode] = useState(false);

    useEffect(() => {
        clearInterval(running_interval);
        clearTimeout(running_timeout);

        if (candidateAlertsData) {
            const trigger_arr_to_validate = [];
            const site_for_trigger_validation = [];
            const temp = [...candidateAlertsData];
            temp.map(row => {
                const { general_status } = row;
                if (general_status !== "routine") {
                    const { trigger_list_arr } = row;
                    
                    if (typeof trigger_list_arr !== "undefined") {
                        const has_new_alert = trigger_list_arr.filter(e => e.validating_status === null);
                        if (has_new_alert.length > 0) {
                            trigger_arr_to_validate.push(has_new_alert);
                            const new_row = { ...row, trigger_list_arr: has_new_alert };
                            site_for_trigger_validation.push(new_row);
                        }
                    }
                }
                return trigger_arr_to_validate;
            });

            const openModal = () => {
                setNewAlertModalOpen(true);
                const temp_timeout = setTimeout(closeModal, 30000); 
                setRunningTimeout(temp_timeout);
            };

            const closeModal = () => {
                setNewAlertModalOpen(false);
                setIsPeaceMode(true);
            };
            
            if (trigger_arr_to_validate.length > 0) {
                if (!is_peace_mode) {
                    // console.log("Playing sound notif...");
                    openModal();
                }
                setNewAlerts(site_for_trigger_validation);
                
                const temp_interval = setInterval(() => {
                    clearTimeout(running_timeout);
                    openModal();
                }, 180000);
                setRunningInterval(temp_interval); 
            } else {
                closeModal();
                clearInterval(running_interval);
                setIsPeaceMode(false);
            }
        }
    }, [candidateAlertsData]);

    const [audio] = useState(new Audio(NotificationSoundFolder));
    useEffect(() => {
        if (isOpen) audio.play();
        else { audio.pause(); audio.currentTime = 0; }
    }, [isOpen]);
    
    const handleClose = () => setNewAlertModalOpen(false);

    return (
        <Dialog 
            fullWidth
            open={isOpen} disableBackdropClick
            onClose={handleClose} 
            aria-labelledby="form-dialog-title"
        >
            <DialogTitle id="form-dialog-title">New alerts for validation</DialogTitle>
            <DialogContent>
                <DialogContentText>
                    Validate the following alert triggers. Acknowledge all alerts on candidate alerts&apos;
                    validation dialog box.
                </DialogContentText>

                {
                    newAlerts && newAlerts.map((row, index) => {
                        const { site_code, trigger_list_arr } = row;

                        return (
                            <Grid container spacing={2} key={site_code} style={{ textAlign: "center" }}>
                                <Typography 
                                    gutterBottom variant="h6"
                                    component={Grid} item xs={12}
                                >
                                    Site Code: {site_code.toUpperCase()}
                                </Typography>
                                {
                                    trigger_list_arr.length > 0 && trigger_list_arr.map((trigger_row, trigger_index) => {
                                        const { trigger_alert_level, ts_updated } = trigger_row;
                                        return (
                                            <Fragment key={trigger_index}>
                                                <Grid item xs={4}>
                                                    <Typography color="textSecondary">
                                                        Trigger
                                                    </Typography>
                                                    <Typography variant="body2" component="p">
                                                        {trigger_alert_level}
                                                    </Typography>
                                                </Grid>
                                                <Grid item xs={8}>
                                                    <Typography color="textSecondary">
                                                        Trigger timestamp
                                                    </Typography>
                                                    <Typography variant="body2" component="p">
                                                        {moment(ts_updated).format("DD MMMM YYYY, HH:mm")}
                                                    </Typography>
                                                </Grid>
                                            </Fragment>
                                        );
                                    })
                                }
                            </Grid>
                        );
                    })
                }
            </DialogContent>
            <DialogActions>
                <Button color="primary" onClick={handleClose}>
                    Close
                </Button>
            </DialogActions>
        </Dialog>
    );
};


export default withMobileDialog()(NewAlertsModal);
