import React, {
    useState, useEffect, useReducer,
    Fragment, useContext
} from "react";

import {
    Button, Grid, Typography,
    IconButton, Avatar, TextField,
    Hidden, Divider, FormControlLabel, 
    Checkbox, FormControl, MenuItem, FormHelperText,
    Select, InputLabel
} from "@material-ui/core";
import { 
    Delete as DeleteIcon,
    Close, Person, AddCircle
} from "@material-ui/icons";
import { useSnackbar } from "notistack";

import MaskedInput, { conformToMask } from "react-text-mask";

import SelectInputForm from "../../reusables/SelectInputForm";
import DynaslopeSiteSelectInputForm from "../../reusables/DynaslopeSiteSelectInputForm";
import { getAllOrganizations, saveContact } from "../ajax";
import { GeneralContext } from "../../contexts/GeneralContext";

const mobile_number_mask = ["(", "+", "6", "3", "9", ")", " ", /\d/, /\d/, "-", /\d/, /\d/, /\d/, "-", /\d/, /\d/, /\d/, /\d/];
const conforming_mobile_mask = ["(", "+", /\d/, /\d/, /\d/, ")", " ", /\d/, /\d/, "-", /\d/, /\d/, /\d/, "-", /\d/, /\d/, /\d/, /\d/];

function prepareGeographicalList (data, category) {
    let list = [];
    if (category === "municipality") {
        const municipalities = data.map(({ municipality, province }) => ({ id: municipality, label: `${municipality}, ${province}` }))
        .sort((a, b) => a.id.localeCompare(b.id));
        list = [...municipalities];
    } else if (["province", "region"].includes(category)) {
        const unique = [...new Set(data.map(x => x[category]).sort())];
        const selection = unique.map(x => ({ id: x, label: x })); 
        list = [...selection];
    }
    return list;
}

function TextMaskCustom (props) {
    const { inputRef, mask, ...other } = props;
  
    return (
        <MaskedInput
            {...other}
            ref={ref => {
                inputRef(ref ? ref.inputElement : null);
            }}
            mask={mask} placeholderChar="x"
            showMask
        />
    );
}

function conformTextMask (mobile_number) {
    const { conformedValue } = conformToMask(mobile_number, conforming_mobile_mask);
    return conformedValue;
}

function removeNumberMask (data, type) {
    const altered_data = [];
    
    if (type === "mobile") {
        data.forEach((row, index) => {
            const { mobile_number } = row;
            mobile_number.sim_num = mobile_number.sim_num.replace(/[()+-\s]/g, "");
            altered_data.push(row);
        });
    } else {
        data.forEach((row, index) => {
            const temp = { ...row };
            delete temp.mobile_id;
            const { landline_id, pte_code } = temp;
            if (landline_id === undefined) temp.landline_id = 0;
            temp.landline_num = temp.landline_num.replace(/[()+-\s]/g, "");
            temp.pte_code = parseInt(pte_code, 10);
            if (Number.isNaN(pte_code)) temp.pte_code = 0;
            altered_data.push(temp);
        });
    }

    return altered_data;
}

function userAffiliation (scope, site_details, organizations) {
    const { municipality, province, region, site_id } = site_details;
    let site = "";
    let location = "";
    let multi_sites = [];

    if (scope === 0 || scope === 1) {
        site = { value: site_id };
    } else if (scope === 2) {
        location = municipality;
    } else if (scope === 3) {
        location = province;
    } else if (scope === 4) {
        location = region;
    } else if (scope === 5) {
        multi_sites = organizations.map(x => ({ value: x.site.site_id }));
    }

    return { site, location, multi_sites };
}

function contactFormValidation (user_details, contact_numbers, affiliation) {
    const { first_name, last_name } = user_details;
    const { scope, site, office, location, multi_sites } = affiliation;
    const { mobile_nums, landline_nums } = contact_numbers;
    let is_sim_num_invalid = false;
    let is_affiliation_invalid = false;
    let is_landline_num_invalid = false;
    let button_state = null;

    mobile_nums.forEach((row, index) => {
        const { mobile_number: { sim_num } } = row;
        if (is_sim_num_invalid === false) {
            if (sim_num === "") { is_sim_num_invalid = true; }
            else { is_sim_num_invalid = sim_num.includes("x"); }
        }
    });

    if (landline_nums.length !== 0)
        landline_nums.forEach((row, index) => {
            const { landline_num } = row;
            if (is_landline_num_invalid === false) {
                if (landline_num === "") { is_landline_num_invalid = true; }
                else { is_landline_num_invalid = landline_num.includes("x"); }
            }
        });

    if (scope === 0 || scope === 1) {
        if (site === "" || office === "") { is_affiliation_invalid = true; }
        else { is_affiliation_invalid = false; }
    } else if (scope === 2 || scope === 3 || scope === 4) {
        if (location === "" || office === "") { is_affiliation_invalid = true; }
        else { is_affiliation_invalid = false; }
    } else if (scope === 5) {
        // is_affiliation_invalid = !multi_sites || office === ""; // temporarily remove multi_sites requirement
        is_affiliation_invalid = office === "";
    }

    if (first_name === "" || last_name === "" || is_sim_num_invalid === true || is_affiliation_invalid === true || is_landline_num_invalid === true) {
        button_state = true;
    } else { button_state = false; }

    return button_state;
}

function reducerFunction (state, action) {
    const { type, category, payload } = action;
    const state_copy = [...state];

    let addend = {
        status: 1, has_delete: true
    };

    if (category === "email") {
        addend = "";
    } else if (category === "mobile") {
        addend = { ...addend, mobile_number: { sim_num: "", mobile_id: 0, gsm_id: "" } };
    } else if (category === "landline") {
        addend = { ...addend, landline_num: "", pte_code: "", area_code: "" };
    }

    switch (type) {
        case "UPDATE":
            if (category === "email") {
                state_copy[payload.key] = payload.value;
            } else if (category === "mobile") {
                if (["sim_num", "gsm_id"].includes(payload.data)) {
                    state_copy[payload.key].mobile_number[payload.data] = payload.value;
                } else {
                    state_copy[payload.key][payload.data] = payload.value;
                }
            } else {
                state_copy[payload.key][payload.data] = payload.value;
            }
            return [...state_copy];
        case "ADD":
            return [...state_copy, addend];
        case "DELETE":
            state_copy.splice(payload.key, 1);
            return [...state_copy];
        default:
            break;
    }

    return [];
}

function getOfficesList (scope, offices_list = []) {
    const o = offices_list.filter(x => x.scope === scope)
    .map(x => ({ id: x.name.toLowerCase(), label: x.name.toUpperCase() }));
    return o;
}

function ContactForm (props) {
    const {
        setContactForm, chosenContact, isEditMode,
        setContactFormForEdit, handleClose, isFromChatterbox,
        setSnackbarKey
    } = props;

    const [offices_list, setOfficesList] = useState([]);

    // let initial_mobiles = [{
    //     mobile_number: { sim_num: "", mobile_id: 0 }, status: 1
    // }];
    let initial_mobiles = [];
    let initial_landlines = [];
    let initial_emails = [];
    let initial_scope = 0;
    let initial_office = "";
    let initial_office_list = [];
    let initial_site = "";
    let initial_location = "";
    let initial_multi_sites = [];
    let initial_status = true;
    let initial_ewi_recipient = true;
    let initial_ground_reminder_recipient = false;
    let initial_ewi_restriction = 0;
    const initial_save_button_state = false;
    let initial_user_details = {
        first_name: "", last_name: "",
        middle_name: "", nickname: "", user_id: 0
    };
    // let initial_area_code = 0;
    
    const { sites } = useContext(GeneralContext);
    if (isEditMode || isFromChatterbox) {
        const {
            mobile_numbers, ewi_recipient, ewi_restriction, landline_numbers,
            emails, first_name, last_name, middle_name, nickname,
            user_id, organizations, status, ground_reminder_recipient
        } = chosenContact;
        initial_user_details = { first_name, last_name, middle_name, nickname, user_id };
        
        if (organizations.length !== 0) {
            const { scope, name } = organizations[0].organization;
            const site_details = organizations[0].site;
            initial_scope = scope;
            initial_office = name;
            initial_office_list = getOfficesList(scope, offices_list);
            const { site, location, multi_sites } = userAffiliation(scope, site_details, organizations);
            initial_site = site;
            initial_location = location;
            initial_multi_sites = multi_sites;
            // const { value } = site;
            // const temp_area_code = sites.filter(x => x.site_id === value);
            // initial_area_code = temp_area_code[0].area_code;
        }

        if (ewi_restriction !== null) {
            if (ewi_restriction.length !== 0) {
                const { alert_level } = ewi_restriction;
                initial_ewi_restriction = alert_level;
            }
        }
        const updated_mobile_numbers = mobile_numbers.map((row, index) => {
            const new_data = JSON.parse(JSON.stringify(row));
            const { mobile_number: { sim_num } } = new_data;
            new_data.mobile_number.sim_num = conformTextMask(sim_num);
            return new_data;
        });

        initial_mobiles = updated_mobile_numbers;
        initial_landlines = landline_numbers;
        initial_emails = emails;
        initial_ewi_recipient = ewi_recipient === 1;
        initial_ground_reminder_recipient = ground_reminder_recipient === 1;
        initial_status = status === 1;
    }

    const municipalities = prepareGeographicalList(sites, "municipality");
    const provinces = prepareGeographicalList(sites, "province");
    const regions = prepareGeographicalList(sites, "region");

    const scope_list = [
        { id: 0, label: "Community" },
        { id: 1, label: "Barangay" },
        { id: 2, label: "Municipal", select_label: "Municipality", list: municipalities },
        { id: 3, label: "Provincial", select_label: "Province", list: provinces },
        { id: 4, label: "Regional", select_label: "Region", list: regions },
        { id: 5, label: "National" }
    ];

    const restriction_list = [
        { id: 0, label: "No restrictions" },
        { id: -1, label: "Do not send on extended and routine monitoring" },
        { id: 1, label: "Do not send on Alert 1" },
        { id: 2, label: "Do not send on Alert 2 and below" }
    ];
    const [user_details, setUserDetails] = useState(initial_user_details);
    const [scope, setScope] = useState(initial_scope);
    const [site, setSite] = useState(initial_site);
    const [multi_sites, setMultiSites] = useState(initial_multi_sites);
    const [location, setLocation] = useState(initial_location);
    const [offices, setOffices] = useState(initial_office_list);
    const [office, setOffice] = useState(initial_office);
    const [mobile_nums, setMobileNums] = useReducer(reducerFunction, initial_mobiles);
    const [landline_nums, setLandlineNums] = useReducer(reducerFunction, initial_landlines);
    const [emails, setEmails] = useReducer(reducerFunction, initial_emails);
    const [is_ewi_recipient, setEwiRecipient] = useState(initial_ewi_recipient);
    const [is_ground_reminder_recipient, setGroundReminderRecipient] = useState(initial_ground_reminder_recipient);
    const [is_active, setIsActive] = useState(initial_status);
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    const [restriction, setRestriction] = useState(initial_ewi_restriction);
    const [save_button_state, setSaveButtonState] = useState(initial_save_button_state);
    const [default_area_code, setDefaultAreaCode] = useState("");

    const closeButtonFn = () => {
        if (isFromChatterbox) handleClose();
        if (isEditMode) return setContactFormForEdit(false);
        return setContactForm(false);
    };

    const nameChangeHandler = attr => event => {
        setUserDetails({ ...user_details, [attr]: event.target.value });
    };

    const ewiRecipientHandler = event => setEwiRecipient(event.target.checked);
    const groundReminderRecipientHandler = event => setGroundReminderRecipient(event.target.checked);
    const isActiveHandler = event => setIsActive(event.target.checked);

    const snackBarActionFn = key => (<Button
        color="primary"
        onClick={() => { closeSnackbar(key); }}
    >
        Dismiss
    </Button>);

    useEffect(() => {
        getAllOrganizations(data => {
            setOfficesList(data);
        });
    }, []);

    useEffect(() => {
        if (site && scope < 3) {
            const { value } = site;
            const temp_area_code = sites.filter(x => x.site_id === value);
            setDefaultAreaCode(temp_area_code[0].area_code);
        } else setDefaultAreaCode("");
    }, [site, scope]);

    useEffect(() => {
        if (location && scope < 4) {
            const attr = scope_list[scope].select_label.toLowerCase();
            const temp = sites.filter(x => x[attr] === location);
            if (temp.length > 0) setDefaultAreaCode(temp[0].area_code);
        } else setDefaultAreaCode("");
    }, [location, scope]);

    const [loc_list, setLocList] = useState([]);
    useEffect(() => {
        if (scope !== "") {
            if (!isEditMode) {
                setSite("");
                setLocation("");
                setOffice("");
            }

            const o = getOfficesList(scope, offices_list);
            setOffices(o);
            setLocList(scope_list[scope].list || []);
        }
    }, [scope, offices_list]);

    useEffect(() => {
        let button_state = true;
        const affiliation = {
            scope, site, office, location, multi_sites
        };

        const contact_numbers = {
            mobile_nums, landline_nums
        };

        button_state = contactFormValidation(user_details, contact_numbers, affiliation);
        setSaveButtonState(button_state);
    }, [
        user_details, mobile_nums, landline_nums,
        scope, site, office, location, multi_sites
    ]);

    const queueUpdatingSnackbar = () => {
        if (!isFromChatterbox) {
            const key = enqueueSnackbar(
                "Updating contacts list...",
                { variant: "warning", persist: true }
            );
        
            setSnackbarKey(key);
        }
    };

    const handleLandlineNumbers = (event, index) => {
        const data = {
            type: "UPDATE",
            payload: {
                data: "landline_num",
                value: event.target.value,
                key: index
            },
            category: "landline"
        };
        setLandlineNums(data);
    };

    const handlePTEcode = (event, index) => {
        const data = {
            type: "UPDATE",
            payload: {
                data: "pte_code",
                value: event.target.value,
                key: index
            },
            category: "landline"
        };
        setLandlineNums(data);
    };

    const handleAreaCode = (event, index) => {
        const data = {
            type: "UPDATE",
            payload: {
                data: "area_code",
                value: event.target.value,
                key: index
            },
            category: "landline"
        };
        setLandlineNums(data);
    };

    const saveFunction = () => {
        const mobile_numbers = removeNumberMask(mobile_nums, "mobile");
        const landline_numbers = removeNumberMask(landline_nums, "landline");
        const ewi_recipient = is_ewi_recipient ? 1 : 0;
        const ground_reminder_recipient = is_ground_reminder_recipient ? 1 : 0;
        const active = is_active ? 1 : 0;
        const final_data = {
            user: {
                ...user_details,
                emails,
                ewi_recipient,
                ground_reminder_recipient,
                restriction,
                status: active
            },
            affiliation: {
                site,
                location,
                scope,
                office,
                multi_sites
            },
            contact_numbers: {
                mobile_numbers,
                landline_numbers
            }
        };

        const snack_key = enqueueSnackbar(
            "Saving contact...",
            {
                variant: "warning",
                persist: true,
                action: snackBarActionFn
            }
        );

        closeButtonFn();
    
        saveContact(final_data, data => {
            const { status, message } = data;
            let variant;
            closeSnackbar(snack_key);
            if (status === true) {
                queueUpdatingSnackbar();
                variant = "success";
            } else {
                variant = "error";
            }

            enqueueSnackbar(
                message,
                {
                    variant,
                    autoHideDuration: 5000,
                    action: snackBarActionFn
                }
            );
        });
    };
 
    return (
        <Grid
            container spacing={1} 
            alignItems="center" justify="space-evenly"
        >
            {
                !isFromChatterbox && <Fragment>
                    <Grid item xs={2} style={{ textAlign: "-webkit-center" }}>
                        <Avatar><Person /></Avatar>
                    </Grid>

                    <Grid item xs={9}>
                        <Typography variant="h5" align="center">
                            Contact Form
                        </Typography>
                    </Grid>
    
                    <Grid item xs={1} >
                        <IconButton edge="start" onClick={closeButtonFn}>
                            <Close />
                        </IconButton>
                    </Grid>

                    <Grid item xs={12} style={{ padding: "12px 4px" }} >
                        <Divider />
                    </Grid>
                </Fragment>
            }

            <Grid item xs={12}>
                <Typography variant="h6" align="center">
                    Personal Details
                </Typography>
            </Grid>

            <Grid item xs={6}>
                <TextField
                    label="First Name"
                    value={user_details.first_name}
                    onChange={nameChangeHandler("first_name")}
                    margin="dense"
                    required
                    error={user_details.first_name === ""}
                />
            </Grid>

            <Grid item xs={6}>
                <TextField
                    label="Last Name"
                    value={user_details.last_name}
                    onChange={nameChangeHandler("last_name")}
                    margin="dense"
                    required
                    error={user_details.last_name === ""}
                />
            </Grid>

            <Grid item xs={6}>
                <TextField
                    label="Middle Name"
                    value={user_details.middle_name || ""}
                    onChange={nameChangeHandler("middle_name")}
                    margin="dense"
                />
            </Grid>

            <Grid item xs={6}>
                <TextField
                    label="Nickname"
                    value={user_details.nickname || ""}
                    onChange={nameChangeHandler("nickname")}
                    margin="dense"
                />
            </Grid>

            <Grid item xs={12}>
                <Typography variant="h6" align="center">
                    Affiliation
                </Typography>
            </Grid>

            <Grid item xs={12}>
                <SelectInputForm
                    div_id="scope"
                    label="Scope"
                    changeHandler={event => setScope(event.target.value)}
                    value={scope}
                    list={scope_list}
                    mapping={{ id: "id", label: "label" }}
                    required
                    error={scope === ""}
                />
            </Grid>

            {
                [0, 1].includes(scope) && (
                    <Grid item xs={6}>
                        <DynaslopeSiteSelectInputForm
                            value={site}
                            changeHandler={value => setSite(value)}
                        />
                    </Grid>
                )
            }

            {
                [2, 3, 4].includes(scope) && (
                    <Grid item xs={6}>
                        <SelectInputForm
                            div_id="location"
                            label={scope_list[scope].select_label}
                            changeHandler={event => setLocation(event.target.value)}
                            value={location}
                            list={loc_list}
                            mapping={{ id: "id", label: "label" }}
                            required
                            error={location === ""}
                        />
                    </Grid>
                )
            }

            {
                scope === 5 && <Grid item xs={12}>
                    <DynaslopeSiteSelectInputForm 
                        value={multi_sites}
                        changeHandler={value => setMultiSites(value)}
                        isMulti   
                        isClearable
                        enableSelectAll           
                    />
                </Grid>
            }

            {
                offices.length > 0 && <Grid item xs={6}>
                    <SelectInputForm
                        div_id="office"
                        label="Office"
                        changeHandler={event => setOffice(event.target.value)}
                        value={office}
                        list={offices}
                        mapping={{ id: "id", label: "label" }}
                        required
                        error={office === ""}
                    />
                </Grid>
            }

            <Grid item xs={6} style={{ textAlign: "center" }}>
                <FormControl>
                    <FormControlLabel
                        control={
                            <Checkbox
                                checked={is_active}
                                onChange={isActiveHandler}
                                value="checkedA"
                                color="primary"
                            />
                        }
                        label="Active"
                    />
                </FormControl>
            </Grid>
            
            <Grid item xs={6} style={{ textAlign: "center" }}>
                <FormControl>
                    <FormControlLabel
                        control={
                            <Checkbox
                                checked={is_ewi_recipient}
                                onChange={ewiRecipientHandler}
                                value="checkedB"
                                color="primary"
                            />
                        }
                        label="EWI Recipient"
                    />
                </FormControl>
            </Grid>

            <Grid item xs={12} style={{ textAlign: "center" }}>
                <FormControl>
                    <FormControlLabel
                        control={
                            <Checkbox
                                checked={is_ground_reminder_recipient}
                                onChange={groundReminderRecipientHandler}
                                value="checkedC"
                                color="primary"
                            />
                        }
                        label="Ground Data Reminder Recipient"
                    />
                </FormControl>
            </Grid>

            <Grid item xs={12}>
                <SelectInputForm
                    div_id="ewi_restriction"
                    label="Ewi Restriction"
                    changeHandler={event => setRestriction(event.target.value)}
                    value={restriction}
                    list={restriction_list}
                    mapping={{ id: "id", label: "label" }}
                    required
                    error={restriction === ""}
                />
            </Grid>

            <Grid item xs={12}>
                <Typography variant="h6" align="center">
                    Contact Information
                </Typography>
            </Grid>

            <Grid item xs={12}>
                <Typography variant="body1" align="center">
                    Mobile
                </Typography>
            </Grid>

            {
                mobile_nums.length === 0 && (
                    <Grid item xs={12}>
                        <Typography variant="body2" color="textSecondary" align="center" paragraph>
                            No mobile number saved
                        </Typography>
                    </Grid>
                )
            }

            {
                mobile_nums.map((row, index) => {
                    const { mobile_number: { sim_num, gsm_id }, status, has_delete } = row;
                    const enable_delete = typeof has_delete !== "undefined";
                    let is_sim_num_invalid = true;
                    if (sim_num === "") {
                        is_sim_num_invalid = true;
                    } else {
                        is_sim_num_invalid = sim_num.includes("x");
                    }
                    return (
                        <Fragment key={index}>
                            {
                                index > 0 && <Grid item xs={12}>
                                    <Divider style={{ margin: "8px 0" }}/>
                                </Grid>
                            }

                            <Grid item xs={8}>
                                <FormControl fullWidth>
                                    <TextField
                                        label="Mobile Number"
                                        value={sim_num}
                                        onChange={event => setMobileNums({
                                            type: "UPDATE",
                                            payload: {
                                                data: "sim_num",
                                                value: event.target.value,
                                                key: index
                                            },
                                            category: "mobile"
                                        })}
                                        InputProps={{
                                            inputComponent: TextMaskCustom,
                                            inputProps: { mask: mobile_number_mask }
                                        }}
                                        required
                                        error={is_sim_num_invalid}
                                    />
                                </FormControl>
                            </Grid>
                            
                            <Grid item xs={4}>
                                <SelectInputForm
                                    div_id="network"
                                    label="Network"
                                    changeHandler={event => setMobileNums({
                                        type: "UPDATE",
                                        payload: {
                                            data: "gsm_id",
                                            value: event.target.value,
                                            key: index
                                        },
                                        category: "mobile"
                                    })}
                                    value={gsm_id}
                                    list={[{ id: 2, label: "Globe/TM" }, { id: 3, label: "Smart/TNT/Sun" }]}
                                    mapping={{ id: "id", label: "label" }}
                                    required
                                    error={gsm_id === ""}
                                />
                            </Grid>

                            <Grid item xs={enable_delete ? 8 : 12} style={{ textAlign: "center" }}>
                                <FormControlLabel
                                    style={{ margin: 0 }}
                                    control={
                                        <Checkbox
                                            checked={Boolean(status)}
                                            onChange={event => setMobileNums({
                                                type: "UPDATE",
                                                payload: {
                                                    data: "status",
                                                    value: event.target.checked ? 1 : 0,
                                                    key: index
                                                },
                                                category: "mobile"
                                            })}
                                            color="primary"
                                        />
                                    }
                                    label="Active"
                                />
                            </Grid>

                            {
                                enable_delete && (
                                    <Grid item xs={4} style={{ textAlign: "center" }}>
                                        <IconButton
                                            edge="end"
                                            size="small"
                                            aria-label="delete"
                                            title="Delete number input"
                                            onClick={() => setMobileNums({
                                                type: "DELETE",
                                                payload: { key: index }
                                            })}
                                        >
                                            <DeleteIcon />
                                        </IconButton>
                                    </Grid>
                                )
                            }
                        </Fragment>
                    );
                })
            }

            <Grid item xs={12} style={{ textAlign: "right" }}>
                <Button 
                    variant="contained"
                    color="primary"
                    size="small"
                    startIcon={<AddCircle />}
                    onClick={() => setMobileNums({ type: "ADD", category: "mobile" })}
                >
                    Add number
                </Button>
            </Grid>

            <Grid item xs={12}>
                <Typography variant="body1" align="center">
                    Landline
                </Typography>
            </Grid>

            {
                landline_nums.length === 0 && (
                    <Grid item xs={12}>
                        <Typography variant="body2" color="textSecondary" align="center" paragraph>
                            No landline number saved
                        </Typography>
                    </Grid>
                )
            }

            {
                landline_nums.map((row, index) => {
                    const {
                        landline_num, pte_code, area_code
                    } = row;

                    let is_landline_valid = true;
                    if (landline_num === "") is_landline_valid = true;
                    else is_landline_valid = landline_num.includes("x");

                    return (
                        <Fragment key={`landline-${ index}`}>
                            <Grid item xs={3}>
                                <FormControl fullWidth>
                                    <TextField
                                        label="Area Code"
                                        value={area_code || default_area_code}
                                        onChange={e => handleAreaCode(e, index)}
                                        required
                                        error={default_area_code === ""}
                                    />
                                </FormControl>
                            </Grid>

                            <Grid item xs={3} style={{ textAlign: "center" }}>
                                <FormControl fullWidth>
                                    <InputLabel id="select-pte-cod-labele">PTE Code</InputLabel>
                                    <Select
                                        labelId="select-pte-code-label"
                                        id="select-pte-code"
                                        required
                                        value={pte_code || 0}
                                        onChange={event => handlePTEcode(event, index)}
                                    >
                                        <MenuItem value={0}>None</MenuItem>
                                        <MenuItem value={3}>3 - Bayan Telco</MenuItem>
                                        <MenuItem value={5}>5 - Eastern Comms. PH</MenuItem>
                                        <MenuItem value={6}>6 - ABS-CBN Convergence</MenuItem>
                                        <MenuItem value={7}>7 - Globe Telecom, Inc.</MenuItem>
                                        <MenuItem value={8}>8 - PLDT, Inc.</MenuItem>
                                    </Select>
                                </FormControl>
                            </Grid>

                            <Grid item xs={5}>
                                <FormControl fullWidth>
                                    <TextField
                                        label="Landline Number"
                                        value={landline_num}
                                        onChange={event => handleLandlineNumbers(event, index)}
                                        InputProps={{
                                            inputComponent: TextMaskCustom,
                                            inputProps: { mask: [/\d/, /\d/, /\d/, "-", /\d/, /\d/, /\d/, /\d/] }
                                        }}
                                        required
                                        error={is_landline_valid}
                                    />
                                </FormControl>
                            </Grid>

                            <Grid item xs={1} style={{ textAlign: "center" }}>
                                <IconButton
                                    edge="end"
                                    size="small"
                                    aria-label="delete"
                                    onClick={() => setLandlineNums({
                                        type: "DELETE",
                                        payload: { key: index }
                                    })}
                                >
                                    <DeleteIcon />
                                </IconButton>
                            </Grid>
                                
                        </Fragment>
                    );
                })
            }

            <Grid item xs={12} style={{ textAlign: "right" }}>
                <Button 
                    variant="contained"
                    color="primary"
                    size="small"
                    startIcon={<AddCircle />}
                    onClick={() => setLandlineNums({ type: "ADD", category: "landline" })}
                >
                    Add number
                </Button>
            </Grid>

            <Grid item xs={12}>
                <Typography variant="body1" align="center">
                    Email
                </Typography>
            </Grid>

            {
                emails.length === 0 && (
                    <Grid item xs={12}>
                        <Typography variant="body2" color="textSecondary" align="center" paragraph>
                            No email address saved
                        </Typography>
                    </Grid>
                )
            }

            {
                emails.map((email, index) => (
                    <Fragment key={index}>
                        <Grid item xs={10}>
                            <FormControl fullWidth>
                                <TextField
                                    label="Email"
                                    value={email.email}
                                    onChange={event => setEmails({
                                        type: "UPDATE",
                                        payload: {
                                            value: event.target.value,
                                            key: index
                                        },
                                        category: "email"
                                    })}
                                />
                            </FormControl>
                        </Grid>

                        <Grid item xs={2} style={{ textAlign: "center" }}>
                            <IconButton
                                edge="end"
                                size="small"
                                aria-label="delete"
                                onClick={() => setEmails({
                                    type: "DELETE",
                                    payload: { key: index }
                                })}
                            >
                                <DeleteIcon />
                            </IconButton>
                        </Grid>
                    </Fragment>
                ))
            }

            <Grid item xs={12} style={{ textAlign: "right" }}>
                <Button 
                    variant="contained"
                    color="primary"
                    size="small"
                    startIcon={<AddCircle />}
                    onClick={() => setEmails({ type: "ADD", category: "email" })}
                >
                    Add email
                </Button>
            </Grid>

            <Grid item xs={12} style={{ padding: "16px 4px" }} >
                <Divider />
            </Grid>

            <Grid item xs={12} style={{ textAlign: "right" }}>
                <Button 
                    color="secondary" 
                    variant="contained"
                    style={{ marginRight: 6 }} 
                    onClick={() => saveFunction()}
                    disabled={save_button_state}
                >
                    Save
                </Button>
                <Button variant="contained" onClick={closeButtonFn}>
                    Cancel
                </Button>
            </Grid>
        </Grid>
    );
}

export default React.memo(ContactForm);