import React, { useState, Fragment } from "react";
import {
    List, ListItem, ListItemAvatar,
    ListItemText, Avatar, Grid, 
    Typography, makeStyles, Paper, Dialog,
    DialogTitle, DialogContent, DialogActions,
    Button, Divider
} from "@material-ui/core";
import { isWidthUp } from "@material-ui/core/withWidth";

import moment from "moment";
import { useSnackbar } from "notistack";

import GenericAvatar from "../../../images/generic-user-icon.jpg";
import { cancelQueuedMessage } from "../ajax";
import { getCurrentUser } from "../../sessions/auth";

const useStyles = makeStyles(theme => ({
    inline: {
        display: "inline",
    },
    overflowEllipsis: {
        fontSize: "inherit",
        textOverflow: "ellipsis",
        whiteSpace: "nowrap",
        overflow: "hidden"
    },
    link: { textDecoration: "none" },
    noFlexGrow: { flexGrow: 0 },
    hidden: { display: "none !important" },
    sentIcon: { fontSize: "1.10rem" }
}));

function CancelQueueModal (props) {
    const {
        onClose, open, chosenMessage
    } = props;
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    const loading_message = "Canceling queued message...";
    const success_message = "Queued message succesfully canceled!";
    const error_message = "Error canceling queued message...";

    const {
        ts, sms_msg, ts_sending,
        task_id, recipients
    } = chosenMessage;
    const current_user = getCurrentUser();

    const handleClose = () => {
        onClose();
    };

    const snackBarActionFn = key => {
        return (<Button
            color="primary"
            onClick={() => { closeSnackbar(key); }}
        >
            Dismiss
        </Button>);
    };

    const onCancelClick = () => {
        const payload = {
            is_ewi: false,
            task_id,
            user_id: current_user.user_id
        };

        const loading_snackbar = enqueueSnackbar(
            loading_message,
            {
                variant: "warning",
                persist: true
            }
        );

        onClose();

        cancelQueuedMessage(payload, response => {
            closeSnackbar(loading_snackbar);
            const { message, status } = response;
            let temp = message;
            let variant;

            if (status) {
                variant = "success";
                temp = success_message;
            } else variant = "error";
        
            enqueueSnackbar(
                temp,
                {
                    variant,
                    autoHideDuration: 7000,
                    action: snackBarActionFn
                }
            );
        }, () => {
            closeSnackbar(loading_snackbar);
            enqueueSnackbar(
                error_message,
                {
                    variant: "error",
                    autoHideDuration: 7000,
                    action: snackBarActionFn
                }
            );
        });
    };

    return (
        <Dialog onClose={handleClose} aria-labelledby="simple-dialog-title" open={open}>
            <DialogTitle id="simple-dialog-title">Queued Message</DialogTitle>
            <DialogContent>
                <Grid container>
                    <Typography variant="body2" component={Grid} item xs={6}>
                        Created: <strong>{moment(ts).format("D MMM YYYY, HH:mm")}</strong>
                    </Typography>

                    <Typography variant="body2" component={Grid} item xs={6} align="right">
                        Sending time: <strong>{moment(ts_sending).format("D MMM YYYY, HH:mm")}</strong>
                    </Typography>
                </Grid>

                <Divider style={{ margin: "12px 0" }}/>

                <Typography variant="body1" gutterBottom>
                    Recipient(s): <strong>{recipients}</strong>
                </Typography>

                <Typography variant="body1">
                    {sms_msg}
                </Typography>
            </DialogContent>
            <DialogActions>
                <Button onClick={onCancelClick} color="secondary">
                    Cancel Sending
                </Button>
                <Button onClick={handleClose} color="primary">
                    Close
                </Button>
            </DialogActions>
        </Dialog>
    );
}

function formatRecipients (recipient_list_preview, recipient_list) {
    let preview = recipient_list_preview.map(x => {
        const { first_name, last_name } = x.users[0].user;
        return `${first_name } ${ last_name}`;
    }).join(", ");

    if (recipient_list_preview.length === 5) preview = `${preview} and ${recipient_list.length - 5} others`;

    return preview;
}

// eslint-disable-next-line max-params
function QueuedRow (row, props, openOptionsModal, index) {
    const { ts_sending, sms_msg, recipient_list_preview, recipient_list } = row;
    const { classes, width } = props;

    const recipients = formatRecipients(recipient_list_preview, recipient_list);

    const on_option_button_click = e => {
        e.preventDefault();
        const temp = { ...row, recipients };
        openOptionsModal(temp);
    };

    return <ListItem alignItems="center" button key={`i${ index}`} onClick={on_option_button_click}>
        <ListItemAvatar>
            <Avatar alt="User" src={GenericAvatar} className={classes.bigAvatar} />
        </ListItemAvatar>
        <ListItemText
            primary={
                <Grid container spacing={0} justify="flex-start" alignItems="center">
                    <Grid item xs className={classes.noFlexGrow}>
                        <Typography 
                            variant="body1" 
                            className={classes.overflowEllipsis}
                            component="div"
                            style={{ display: "flex", alignItems: "center" }}
                        >
                            {/* {senders.map((row, i) => {
                                return (
                                    <Fragment key={`${row.sender}-${mobile_id}`}>
                                        {senders.length > 1 && row.inactive && InactiveIcon}
                                        <span title={row.org} 
                                            style={{ paddingRight: 4 }}
                                        >
                                            {row.sender}{i < senders.length - 1 && ","}
                                        </span>
                                    </Fragment>
                                );
                            })} */}
                            { recipients }
                        </Typography>
                    </Grid>
                
                    <Grid 
                        item xs={6} sm
                        container 
                        justify={isWidthUp("sm", width) ? "flex-end" : "flex-start"}
                        alignItems="center"
                    >
                        {/* {
                            (senders.length === 1 && senders[0].inactive) && InactiveIcon
                        } */}

                        <Typography 
                            variant="subtitle2" 
                            color="textSecondary"
                        >
                            Sending time: {moment(ts_sending).format("D MMM YYYY, HH:mm")}
                        </Typography>
                    </Grid>
                    {/* <Grid 
                        item xs 
                        style={{ 
                            textAlign: "right",
                            flexGrow: is_desktop ? 0 : 1,
                            flexBasis: "auto" 
                        }}
                    >
                        <IconButton size="small" onClick={on_option_button_click}>
                            <MoreVert />
                        </IconButton>
                    </Grid> */}
                </Grid>
            }
            primaryTypographyProps={{
                className: classes.overflowEllipsis
            }}
            secondary={
                // messages.length > 0 ? (
                //     SecondaryInformation(classes, messages[0])
                // ) : (
                //     <div style={{ textAlign: "center" }}>{no_convo_message}</div>
                // )
                sms_msg
            }
            secondaryTypographyProps={{ component: "div" }}
        />
    </ListItem>;
}

function QueuedMessages (comp_props) {
    const { messagesArr, hidden } = comp_props;
    const classes = useStyles();
    const props = { ...comp_props, classes };

    const [open_options, setOpenOptions] = useState(false);
    const [chosen_message, setChosenMessage] = useState(null);

    const openOptionsModal = row => {
        setChosenMessage(row);
        setOpenOptions(true);
    };

    const handleOptionsClose = value => {
        setOpenOptions(false);
    };
    
    return (
        <Fragment>
            {
                messagesArr.length === 0 ? (
                    <Paper
                        style={{
                            height: "30vh", padding: 60, display: "flex",
                            justifyContent: "center", alignItems: "center",
                            background: "gainsboro", border: "4px solid #CCCCCC",
                            marginTop: 30
                        }}
                        className={ hidden ? classes.hidden : "" }
                    >
                        <div>No queued messages</div>
                    </Paper>
                ) : (
                    <List dense className={ hidden ? classes.hidden : "" }>
                        {
                            messagesArr.map((row, index) => QueuedRow(row, props, openOptionsModal, index))
                        }
                    </List>
                    // <div className={ hidden ? classes.hidden : "" }>
                    //     {
                    //         messagesArr.map((row, index) => QueuedRow(row, props, index))
                    //     }
                    // </div>
                )
            }

            {
                chosen_message !== null && <CancelQueueModal
                    open={open_options}
                    setOpenOptions={setOpenOptions}
                    chosenMessage={chosen_message}
                    onClose={handleOptionsClose}
                />
            }
        </Fragment>
    );
}

export default QueuedMessages;