import React, { useState, useEffect, Fragment } from "react";

import {
    Dialog, DialogTitle, DialogContent,
    DialogContentText, DialogActions,
    Button, withMobileDialog, makeStyles,
    Radio, RadioGroup, FormControlLabel,
    FormLabel, CircularProgress, Grid,
    TextField
} from "@material-ui/core";
import { MuiPickersUtilsProvider, KeyboardDateTimePicker } from "@material-ui/pickers";
import MomentUtils from "@date-io/moment";

import axios from "axios";
import moment from "moment";

import GeneralStyles from "../../../GeneralStyles";
import SelectMultipleWithSuggest from "../../reusables/SelectMultipleWithSuggest";
import { SlideTransition, FadeTransition } from "../../reusables/TransitionList";
import { host } from "../../../config";
import { handleUpdateInsertTags, handleDeleteTags } from "../ajax";
import { getCurrentUser } from "../../sessions/auth";
import { mobileUserFormatter } from "./MessageList";
import DynaslopeSiteSelectInputForm from "../../reusables/DynaslopeSiteSelectInputForm";

const useStyles = makeStyles(theme => GeneralStyles(theme));

export function prepareContactPerson (mobileDetails) {
    const { sim_num, users } = mobileDetails;
    let name = sim_num;

    if (users.length > 0) {
        const formatted_users = mobileUserFormatter(users, false);
        const { sender_arr } = formatted_users;
        const temp = [];
        sender_arr.forEach(row => {
            const { sender, level, inactive } = row;
            if (!inactive) {
                const position = level !== null ? `${level} ` : "";
                temp.push(`${position}${sender}`);
            }
        });

        if (temp.length > 0) name = temp.join(", ");
        if (temp.length > 1) name += "(shared number)";
    }

    return name;
}

function useFetchTagOptions (tag_selection) {
    const [tags, update_tags] = useState([]);
    const cancel_token = axios.CancelToken;
    const source = cancel_token.source();

    useEffect(() => {
        const api_link = `${host}/api/chatterbox/get_message_tag_options/sms${tag_selection}_users`;

        axios.get(api_link, { cancelToken: source.token })
        .then(({ data }) => {
            const arr = data.map(row => ({
                value: row.tag_id,
                label: row.tag
            }));

            update_tags(arr);
        })
        .catch(error => {
            console.log(error);
        });

        // return source.cancel();
    }, [tag_selection]);

    return tags;
}

function preparePassedTags (tag_obj) {
    const { tags } = tag_obj;

    const tag_arr = tags.map(x => ({
        value: x.tag_id,
        label: x.tag.tag
    }));

    return tag_arr;
}

function ChatterboxTagModal (props) {
    const {
        fullScreen, isOpen,
        closeHandler, tagOption,
        tagObject, mobileDetails, message
    } = props;

    const classes = useStyles();

    const tag_options = useFetchTagOptions(tagOption);
    const [tags, update_tags] = useState(null);
    const [orig_tags, setOrigTags] = useState([]);

    const [has_fyi_tag, setHasFyiTag] = useState(false);
    const [has_permission_tag, setHasPermissionTag] = useState(false);
    const [has_rainfall_tag, setHasRainfallTag] = useState(false);
    const [has_gnd_meas_resend, setHasGndMeasResend] = useState(false);
    const [has_ewi_response, setHasEWIResponse] = useState(false);

    const [sites, setSites] = useState(null);
    const [fyi_purpose, setFyiPurspose] = useState("");
    const [permission_purpose, setPermissionPurpose] = useState("");
    const [attach_narrative_to, setAttachNarrativeTo] = useState("");
    const [gnd_meas_sender, setGndMeasSender] = useState("");
    const [gnd_meas_ts_received, setGndMeasTsReceived] = useState(null);

    const is_saved_number = mobileDetails.users.length > 0;

    useEffect(() => {
        if (tags !== null) {
            setHasFyiTag(tags.some(x => x.label === "#AlertFYI"));
            setHasPermissionTag(tags.some(x => x.label === "#Permission"));
            setHasRainfallTag(tags.some(x => x.label === "#RainInfo"));
            setHasGndMeasResend(tags.some(x => x.label === "#GroundMeasResend"));
            setHasEWIResponse(tags.some(x => x.label === "#EwiResponse"));
        } else {
            setHasFyiTag(false);
            setHasPermissionTag(false);
            setHasRainfallTag(false);
            setHasGndMeasResend(false);
            setHasEWIResponse(false);
        }
    }, [tags]);

    const [is_disabled, setIsDisabled] = useState(false);

    useEffect(() => {
        let bool = false;
        if (is_saved_number) {
            if (has_fyi_tag) {
                if (sites === null || fyi_purpose === "") bool = true;
            }

            if (has_permission_tag) {
                if (sites === null || permission_purpose === "") bool = true;
            }

            if (has_rainfall_tag || has_ewi_response) {
                bool = sites === null && attach_narrative_to === "specified-sites" || attach_narrative_to === "";
            }

            if (has_gnd_meas_resend) {
                bool = sites === null || gnd_meas_sender === "" || gnd_meas_ts_received === null;
            }
        } else {
            bool = true;
        }

        setIsDisabled(bool);
    }, [
        has_fyi_tag, sites, fyi_purpose, 
        has_permission_tag, permission_purpose,
        has_rainfall_tag, attach_narrative_to,
        has_gnd_meas_resend, gnd_meas_sender, gnd_meas_ts_received,
        has_ewi_response
    ]);

    useEffect(() => {
        const tag_arr = preparePassedTags(tagObject);
        update_tags(tag_arr);
        setOrigTags(tag_arr);
    }, [tagObject]);

    const update_tags_fn = value => update_tags(value);

    const submitTagHandler = () => {
        const { id, source, ts } = tagObject;
        let ts_message = ts;

        let contact_person = null;
        let site_id_list = [];

        if (!has_fyi_tag) {
            mobileDetails.users.forEach(row => {
                const { user } = row;

                const user_organizations = user.organizations;
                user_organizations.forEach(org => {
                    const { site: { site_id } } = org;
                    !site_id_list.includes(site_id) && site_id_list.push(site_id);
                });
            });

            contact_person = prepareContactPerson(mobileDetails);
        }

        if (has_gnd_meas_resend) {
            contact_person = gnd_meas_sender;
            ts_message = gnd_meas_ts_received.format("YYYY-MM-DD HH:mm:00");
        }
        
        if (has_fyi_tag || has_gnd_meas_resend ||
            ((has_rainfall_tag || has_ewi_response) 
            && attach_narrative_to === "specified-sites")
        ) {
            site_id_list = [];
            site_id_list = sites.map(x => x.value);
        }

        const var_key_id = source === "inbox" ? "inbox_id" : "outbox_id";

        // DELETE MISSING TAGS
        let missing_tags = [];
        if (orig_tags.length > 0) {
            if (tags === null) missing_tags = [...orig_tags];
            else {
                missing_tags = orig_tags.filter(row => !tags.some(x => x.value === row.value));
            }
        }

        if (missing_tags.length > 0) {
            const delete_tag_id_list = missing_tags.map(tag => tag.value);
            const delete_payload = {
                tag_type: `sms${source}_user_tags`,
                tag_details: {
                    [var_key_id]: id,
                    delete_tag_id_list
                }
            };
            // console.log("delete_payload", delete_payload);
            handleDeleteTags(delete_payload, response => {
                console.log("untag response", response);
            });
        }

        // ADD NEW TAGS
        let new_tags = [];
        // eslint-disable-next-line
        if (tags !== null) new_tags = tags.filter((i => a => a !== orig_tags[i] || !++i)(0));
        if (new_tags.length > null) {
            const tag_id_list = new_tags.map(tag => tag.value);

            let final_message = message;
            if (has_fyi_tag) {
                final_message = fyi_purpose;
            } else if (has_permission_tag) {
                final_message = permission_purpose;
            }
            const payload = {
                tag_type: `sms${source}_user_tags`,
                contact_person,
                message: final_message,
                tag_details: {
                    user_id: getCurrentUser().user_id,
                    [var_key_id]: id,
                    tag_id_list,
                    site_id_list,
                    ts: moment().format("YYYY-MM-DD HH:mm:ss"),
                    ts_message
                }
            };
            // console.log("PAYLOAD", payload);
            handleUpdateInsertTags(payload, response => {
                console.log("tag response", response);
            });
        }

        closeHandler();
    };

    return (
        <Dialog
            fullWidth
            fullScreen={fullScreen}
            open={isOpen}
            aria-labelledby="form-dialog-title"
            TransitionComponent={fullScreen ? SlideTransition : FadeTransition}
        >
            <DialogTitle id="form-dialog-title">
                Chatterbox Message Tagging
            </DialogTitle>
            <DialogContent>
                <DialogContentText>
                    Attach or remove tags to a message.
                </DialogContentText>

                <DialogContentText className={classes.dyna_error}>
                    NOTE: You must manually remove any site logs created (if any)
                    by a tag when removing a tag to a message.
                </DialogContentText>

                {
                    tag_options.length === 0 ? <div style={{ 
                        display: "flex", justifyContent: "center"
                    }}>
                        <CircularProgress />
                    </div> : <Fragment>
                        <SelectMultipleWithSuggest
                            label="Tags"
                            options={tag_options}
                            value={tags}
                            changeHandler={update_tags_fn}
                            placeholder="Select tags"
                            renderDropdownIndicator={false}
                            openMenuOnClick
                            isMulti
                        />

                        {
                            has_fyi_tag && <div style={{ marginTop: 18 }}>
                                <DialogContentText color="textPrimary">
                                    Alert FYI
                                </DialogContentText>

                                <DynaslopeSiteSelectInputForm
                                    value={sites}
                                    changeHandler={value => setSites(value)}
                                    isMulti
                                    required
                                />

                                <FormLabel component="legend" style={{ marginTop: 24 }}>Purpose</FormLabel>
                                <RadioGroup
                                    aria-label="fyi-purpose"
                                    name="fyi-purpose"
                                    row
                                    style={{ justifyContent: "space-around" }}
                                    value={fyi_purpose}
                                    onChange={e => setFyiPurspose(e.target.value)}
                                >
                                    <FormControlLabel value="alert raising" control={<Radio />} label="Alert raising" />
                                    <FormControlLabel value="alert lowering" control={<Radio />} label="Alert lowering" />
                                </RadioGroup>
                            </div>
                        }

                        {
                            (has_rainfall_tag || has_ewi_response) && <div style={{ marginTop: 18 }}>
                                <DialogContentText color="textPrimary">
                                    { has_rainfall_tag ? "Rainfall Information" : "EWI Response" }
                                </DialogContentText>

                                <FormLabel component="legend" style={{ marginTop: 12 }}>Attach narrative to</FormLabel>
                                <RadioGroup
                                    aria-label="attach-narrative"
                                    name="attach-narrative"
                                    row
                                    style={{ justifyContent: "space-around", marginBottom: 12 }}
                                    value={attach_narrative_to}
                                    onChange={e => setAttachNarrativeTo(e.target.value)}
                                >
                                    <FormControlLabel value="default-sites" control={<Radio />} label="All sites of stakeholder" />
                                    <FormControlLabel value="specified-sites" control={<Radio />} label="Specify sites" />
                                </RadioGroup>

                                {
                                    attach_narrative_to === "specified-sites" && <DynaslopeSiteSelectInputForm
                                        value={sites}
                                        changeHandler={value => setSites(value)}
                                        isMulti
                                        required
                                    />
                                }
                            </div>
                        }

                        {
                            has_permission_tag && <div style={{ marginTop: 18 }}>
                                <DialogContentText color="textPrimary">
                                    Permission
                                </DialogContentText>

                                <DynaslopeSiteSelectInputForm
                                    value={sites}
                                    changeHandler={value => setSites(value)}
                                    isMulti
                                    required
                                />

                                <FormLabel component="legend" style={{ marginTop: 24 }}>Purpose</FormLabel>
                                <RadioGroup
                                    aria-label="permission-purpose"
                                    name="permission-purpose"
                                    row
                                    style={{ justifyContent: "space-around" }}
                                    value={permission_purpose}
                                    onChange={e => setPermissionPurpose(e.target.value)}
                                >
                                    <FormControlLabel value="alert 3 raising" control={<Radio />} label="Alert 3 Raising" />
                                    <FormControlLabel value="alert 3 lowering" control={<Radio />} label="Alert 3 Lowering" />
                                    <FormControlLabel value="on demand raising" control={<Radio />} label="On Demand Raising" />
                                </RadioGroup>
                            </div>
                        }

                        {
                            has_gnd_meas_resend && <div style={{ marginTop: 18 }}>
                                <DialogContentText color="textPrimary">
                                    Ground Measurement Resend
                                </DialogContentText>

                                <Grid container spacing={2}>
                                    <Grid item xs={12}>
                                        <FormLabel component="legend" style={{ marginBottom: 12 }}>Attach narrative to</FormLabel>
                                        <DynaslopeSiteSelectInputForm
                                            value={sites}
                                            changeHandler={value => setSites(value)}
                                            isMulti
                                            required
                                        />
                                    </Grid>

                                    <Grid item xs={12} sm={6}>
                                        <TextField
                                            required
                                            label="Sender"
                                            value={gnd_meas_sender}
                                            onChange={event => setGndMeasSender(event.target.value)}
                                            fullWidth
                                            title="Name of ground data sender (this will appear on site log)"
                                        />
                                    </Grid>

                                    <Grid item xs={12} sm={6}>
                                        <MuiPickersUtilsProvider utils={MomentUtils}>
                                            <KeyboardDateTimePicker
                                                required
                                                autoOk
                                                label="Timestamp Received"
                                                value={gnd_meas_ts_received}
                                                onChange={x => setGndMeasTsReceived(x)}
                                                ampm={false}
                                                placeholder="2010/01/01 00:00"
                                                format="YYYY/MM/DD HH:mm"
                                                mask="____/__/__ __:__"
                                                clearable
                                                disableFuture
                                                fullWidth
                                                title="Timestamp when data was received (either via Facebook or GSM server)"
                                            />
                                        </MuiPickersUtilsProvider>
                                    </Grid>
                                </Grid>
                            </div>
                        }
                    </Fragment>
                }
            </DialogContent>
            <DialogActions>
                <Button
                    color="primary"
                    onClick={submitTagHandler}
                    disabled={is_disabled}
                >
                    Submit
                </Button>
                <Button onClick={closeHandler}>
                    Cancel
                </Button>
            </DialogActions>
        </Dialog>
    );
}

export default withMobileDialog()(ChatterboxTagModal);
