import React, { useState } from "react";
import {
    Dialog, DialogTitle, DialogContent,
    DialogContentText, DialogActions,
    Button, withMobileDialog, IconButton,
    CircularProgress
} from "@material-ui/core";
import { Close } from "@material-ui/icons";
import SendMessageForm from "../../communication/chatterbox/SendMessageForm";
import { SlideTransition, FadeTransition } from "../../reusables/TransitionList";

function SendEwiSmsModal (props) {
    const {
        fullScreen, modalStateHandler,
        modalState, textboxValue, releaseDetail
    } = props;

    const { 
        release_id, site_code, is_onset_release,
        not_yet_sending_time, is_queued, 
        prescribed_release_time, event_id, is_sms_sent
    } = releaseDetail;

    const show_queue_buttons = !is_onset_release && not_yet_sending_time;

    const [to_trigger_send, setToTriggerSend] = useState(false);
    const clickSend = () => setToTriggerSend(true);

    const [to_trigger_queue, setToTriggerQueue] = useState(false);
    const clickQueue = () => setToTriggerQueue(true);

    const [to_trigger_cancel_queue, setToTriggerCancelQueue] = useState(false);
    const clickCancelQueue = () => setToTriggerCancelQueue(true);

    const [to_disable_send_queue, setToDisableSendQueue] = useState(true);

    const ewiModalTriggers = {
        send: {
            trigger: to_trigger_send,
            revert: () => setToTriggerSend(false)
        },
        queue: {
            trigger: to_trigger_queue,
            revert: () => setToTriggerQueue(false),
        },
        cancel_queue: {
            task_id: is_queued ? is_queued.remarks : null,
            trigger: to_trigger_cancel_queue,
            revert: () => setToTriggerCancelQueue(false)
        },
        disable_send_queue_buttons: setToDisableSendQueue
    };

    return (
        <Dialog
            fullWidth
            fullScreen={fullScreen}
            open={modalState}
            aria-labelledby="form-dialog-title"
            TransitionComponent={fullScreen ? SlideTransition : FadeTransition}
                    
        >
            <DialogTitle id="form-dialog-title">
                <div style={{ display: "flex", alignItems: "center", justifyContent: "space-between" }}>
                    <span>Send Early Warning Information</span>
                    <IconButton 
                        color="inherit" 
                        onClick={modalStateHandler}
                        aria-label="Close"
                        style={{ padding: 0 }}
                    >
                        <Close />
                    </IconButton>
                </div>
            </DialogTitle>
            <DialogContent style={{ overflowY: "auto" }}>
                <DialogContentText>
                    Early warning information recipients for this site is already pre-loaded. Add additional site-related contacts
                    if necessary. Use Chatterbox for non-site-related contacts.
                </DialogContentText>

                <div style={{ marginTop: 20, textAlign: "center" }}>
                    {
                        textboxValue !== null ? <SendMessageForm
                            isMobile={fullScreen}
                            textboxValue={textboxValue}
                            disableQuickSelect
                            releaseId={release_id}
                            eventId={event_id}
                            siteCode={site_code}
                            updateSentStatusObj={releaseDetail}
                            modalStateHandler={modalStateHandler}
                            prescribedReleaseTime={prescribed_release_time}
                            fromEWIModal
                            ewiModalTriggers={ewiModalTriggers}
                        /> : <CircularProgress />
                    }
                </div>
            </DialogContent>
            <DialogActions>
                {
                    show_queue_buttons && !is_queued && (
                        <Button onClick={clickQueue} color="primary" 
                            disabled={textboxValue === null || to_disable_send_queue}>
                            Queue
                        </Button>
                    )
                }
                {
                    show_queue_buttons && is_queued && (
                        <Button color="primary" onClick={clickCancelQueue}>
                            Cancel Queue
                        </Button>
                    )
                }
                {
                    !not_yet_sending_time && <Button onClick={clickSend} color="primary"
                        disabled={textboxValue === null || to_disable_send_queue}>
                        { is_sms_sent ? "Send Again" : "Send" }
                    </Button>
                }
                <Button onClick={modalStateHandler} color="primary">
                    Cancel
                </Button>
            </DialogActions>
        </Dialog>
    );
}

export default withMobileDialog()(SendEwiSmsModal);
