import React, { Fragment, useState, useEffect } from "react";

import {
    Grid, Typography,
    List, ListItem, ListItemAvatar,
    ListItemText, Avatar, Chip,
    Tooltip
} from "@material-ui/core";
import Pagination from "@material-ui/lab/Pagination";
import { Person, Star } from "@material-ui/icons";

import { getUserOrganizations, getUserContactPriority } from "../../../UtilityFunctions";

const MemoizedItem = React.memo(props => {
    const { classes, row, index, onContactClickFn } = props;

    const { user_id, first_name, last_name, organizations } = row;
    const orgs = getUserOrganizations(organizations);
    const is_priority = getUserContactPriority(organizations);

    return (
        <ListItem 
            button
            onClick={onContactClickFn(row, index)}
            key={user_id}
        >
            <ListItemAvatar>
                <Avatar>
                    <Person />
                </Avatar>
            </ListItemAvatar>
            <ListItemText
                primary={
                    <Grid 
                        container
                        spacing={0}
                        justify="flex-start"
                        alignItems="center"
                    >
                        <Grid 
                            item xl
                            style={{ flexGrow: 1 }}
                        >
                            <Typography 
                                variant="body1" 
                                style={{ marginRight: 8 }}
                            >
                                {`${last_name}, ${first_name}`}
                            </Typography>
                        </Grid>
                        {
                            orgs.map((x, i) => {
                                const bool = i === 0;
                                const color = bool ? "secondary" : "primary";

                                if (bool && is_priority) {
                                    return (
                                        <Grid key={i} item xs className={classes.noFlexGrow}>
                                            <Tooltip 
                                                arrow
                                                disableFocusListener
                                                title="Contact Priority"
                                                placement="top"
                                            >
                                                <Chip
                                                    color={color} size="small"
                                                    label={x}
                                                    icon={<Star style={{ color: "gold" }}/>}
                                                />
                                            </Tooltip>
                                        </Grid>
                                    );
                                }

                                return (
                                    <Grid key={i} item xs className={classes.noFlexGrow}>
                                        <Chip color={color} size="small" label={x} />
                                    </Grid>
                                );
                            })
                        }
                    </Grid>
                }
            />
        </ListItem>           
    );
});

function ContactList (props) {
    const {
        contacts, onContactClickFn, classes,
        searchStr
    } = props;

    const max_pages = Math.ceil(contacts.length / 10);

    const [page, setPage] = useState(1);
    const [contacts_array, setContactsArray] = useState([]);
    const handleChange = (event, value) => {
        setPage(value);
    };

    useEffect(() => {
        const temp = contacts.slice((page - 1) * 10, (page - 1) * 10 + 10);
        setContactsArray(temp);
    }, [page, contacts]);

    useEffect(() => setPage(1), [searchStr]);

    return <Fragment>
        <List dense style={{ paddingTop: 0 }}>
            {
                contacts_array.map((row, index) => <MemoizedItem 
                    row={row}
                    index={index + ((page - 1) * 10)}
                    onContactClickFn={onContactClickFn}
                    classes={classes}
                    key={`list-${index}`}
                />)
            }
        </List>

        <Pagination 
            count={max_pages} variant="outlined"
            defaultPage={1} boundaryCount={2}
            page={page}
            shape="rounded" onChange={handleChange}
            style={{
                justifyContent: "center",
                display: "flex"
            }}
        />
    </Fragment>;
}

export default ContactList;