import React, { Fragment, useState, useEffect } from "react";
import {
    IconButton, TextField, Typography,
    Grid, Menu, MenuItem, Divider,
    FormControlLabel, Checkbox, Button,
    FormLabel
} from "@material-ui/core";
import { SendRounded, MoreVert } from "@material-ui/icons";

import { MuiPickersUtilsProvider, KeyboardDateTimePicker } from "@material-ui/pickers";
import MomentUtils from "@date-io/moment";

import RainInfoTemplateModal from "./RainInfoTemplateModal";
import SelectInputForm from "../../reusables/SelectInputForm";
import { getCurrentUser } from "../../sessions/auth";
import { getAllSmsPriority } from "../ajax";
import DynaslopeSiteSelectInputForm from "../../reusables/DynaslopeSiteSelectInputForm";
import AlertFyiTemplateModal from "./AlertFyiTemplateModal";


function MessageInputTextbox (props) {
    const {
        limitRows, value, disableSend,
        messageChangeHandler, sendButtonClickHandler,
        setComposedMessage, fromEWIModal,
        setPriority, setTsSending,
        priority, tsSending,
        sites, setSites
    } = props;
    const limit = limitRows === undefined ? true : limitRows;
    const [is_rainfall_modal_open, setIsRainfallModalOpen] = useState(false);
    const [is_fyi_modal_open, setIsFyiModalOpen] = useState(false);
    const [schedule_sending, setScheduleSending] = useState(false);
    const [priority_list, setPriorityList] = useState([]);

    useEffect(() => {
        if (!fromEWIModal) {
            getAllSmsPriority(data => {
                const list = data.filter(x => Boolean(x.list_as_option));
                const default_prio = data.find(x => x.name === "Default");
                setPriorityList(list);
                setPriority(default_prio.priority_id);
            });
        }
    }, [fromEWIModal]);

    useEffect(() => {
        if (!schedule_sending && setTsSending) setTsSending(null);
    }, [schedule_sending]);

    const [anchor, setAnchor] = useState(null);
    const onMenuClick = event => setAnchor(event.currentTarget);
    const onMenuClose = () => setAnchor(null);

    const [is_more_options, setMoreOptions] = useState(false);

    const onGenerateRainInfoClick = () => {
        setIsRainfallModalOpen(true);
        onMenuClose();
    };

    const onGenerateFyiClick = () => {
        setIsFyiModalOpen(true);
        onMenuClose();
    };

    const onMoreOptionsClick = () => {
        setMoreOptions(true);
        onMenuClose();
    };

    const onCancelOptionsClick = () => {
        setMoreOptions(false);
    };

    const { nickname } = getCurrentUser();
    const message_ender = ` - ${nickname} from PHIVOLCS-DYNASLOPE`;

    const max_chars = 1000;
    const total_length = value.length + message_ender.length;

    const ts_sched_helper_text = schedule_sending && tsSending === null ?
        "Required field" : null;

    return (
        <Fragment>
            <Grid container spacing={1} alignItems="center" style={{ paddingTop: 16 }}>
                <Grid item xs={fromEWIModal ? 12 : 10} sm={fromEWIModal ? 12 : 11}>
                    <TextField
                        id="message_textbox"
                        label="Message"
                        multiline
                        rows="4"
                        rowsMax={ limit ? 4 : 10 }
                        fullWidth
                        margin="dense"
                        variant="filled"
                        inputProps={{ maxLength: max_chars }}
                        value={value}
                        onChange={messageChangeHandler}
                    />
                    <Typography variant="caption" align="right" component="div">
                        <strong>Character count (including signature): {total_length}/{max_chars}</strong>
                    </Typography>        
                </Grid>

                {
                    !fromEWIModal && <Fragment>
                        <Grid item xs={1} container direction="column">
                            <IconButton
                                color="primary"
                                aria-label="Send message"
                                onClick={sendButtonClickHandler}
                                disabled={disableSend || (schedule_sending && tsSending === null)}
                                title="Send message"
                            >
                                <SendRounded />
                            </IconButton>
                    
                            <IconButton
                                color="primary"
                                aria-label="More options"
                                aria-controls="simple-menu"
                                aria-haspopup="true"
                                onClick={onMenuClick}
                                title="More options"
                            >
                                <MoreVert />
                            </IconButton>

                            <Menu
                                id="simple-menu"
                                anchorEl={anchor}
                                keepMounted
                                open={Boolean(anchor)}
                                onClose={onMenuClose} 
                            >
                                <MenuItem onClick={onMoreOptionsClick}>
                                    Queue/Schedule message sending
                                </MenuItem> 
                                <MenuItem onClick={onGenerateRainInfoClick}>
                                    Generate rainfall information
                                </MenuItem>
                                <MenuItem onClick={onGenerateFyiClick} >
                                    Generate alert raising/lowering FYI message
                                </MenuItem>
                            </Menu>
                        </Grid>

                        {
                            is_more_options && <Fragment>
                                <Grid item xs={12}>
                                    <Divider style={{ margin: "12px 0" }}/>
                                </Grid>
                        
                                <Typography variant="body1" component={Grid} item xs={8}>
                                    <strong>Message options</strong>    
                                </Typography>

                                <Typography variant="body2" component={Grid} item xs={4} align="right">
                                    <Button color="primary" onClick={onCancelOptionsClick}>
                                        Hide
                                    </Button>
                                </Typography>

                                <Grid item xs={4} sm={3} style={{ textAlign: "center" }}>
                                    <FormControlLabel
                                        control={<Checkbox
                                            checked={schedule_sending}
                                            onChange={x => setScheduleSending(x.target.checked)}
                                            name="checkedA"
                                        />}
                                        label="Schedule"
                                    />
                                </Grid>

                                <MuiPickersUtilsProvider utils={MomentUtils}>
                                    <Grid item xs={8} sm={6}>
                                        <KeyboardDateTimePicker
                                            autoOk
                                            label="Timestamp of sending"
                                            value={tsSending}
                                            onChange={x => setTsSending(x)}
                                            ampm={false}
                                            placeholder="2010/01/01 00:00"
                                            format="YYYY/MM/DD HH:mm"
                                            mask="____/__/__ __:__"
                                            clearable
                                            fullWidth
                                            disablePast
                                            disabled={!schedule_sending}
                                            helperText={ts_sched_helper_text || ""}
                                            error={Boolean(ts_sched_helper_text)}
                                        />
                                    </Grid>
                                </MuiPickersUtilsProvider>

                                <Grid item xs={12} sm={3}>
                                    <SelectInputForm
                                        div_id="sms-priority"
                                        label="Priority"
                                        list={priority_list}
                                        mapping={{ id: "priority_id", label: "name" }}
                                        value={priority}
                                        changeHandler={event => setPriority(event.target.value)}
                                    />
                                </Grid>

                                {
                                    schedule_sending && priority_list.find(x => x.priority_id === priority).keyword === "rain_info" && (
                                        <Fragment>
                                            <Grid item xs={12}>
                                                <Divider style={{ margin: "12px 0" }}/>
                                            </Grid>

                                            <Typography variant="body2" component={Grid} item xs={12}>
                                                <strong>Rainfall Information Tagging</strong>    
                                            </Typography>

                                            <Grid item xs={12}>
                                                <FormLabel component="legend">Attach narrative to</FormLabel>
                                            </Grid>

                                            <Grid item xs={12}>
                                                <DynaslopeSiteSelectInputForm
                                                    value={sites}
                                                    changeHandler={val => setSites(val)}
                                                    isMulti
                                                    required
                                                />
                                            </Grid>
                                        </Fragment>
                                    )
                                }
                            </Fragment>
                        }
                    </Fragment>
                }
            </Grid>

            <RainInfoTemplateModal
                isOpen={is_rainfall_modal_open} 
                setComposedMessage={setComposedMessage}
                clickHandler={() => setIsRainfallModalOpen(false)}
                maxCharacters={max_chars}
                inputFieldLength={max_chars - message_ender.length}
            />

            <AlertFyiTemplateModal
                isOpen={is_fyi_modal_open} 
                setComposedMessage={setComposedMessage}
                clickHandler={() => setIsFyiModalOpen(false)}
            />
        </Fragment>
    );
}

export default MessageInputTextbox;
