import React, { useState, Fragment, useEffect } from "react";
import { useSnackbar } from "notistack";
import { IconButton, Grid, Button, CircularProgress } from "@material-ui/core";
import { AddBox } from "@material-ui/icons";
import MessageInputTextbox from "./MessageInputTextbox";
import SelectMultipleWithSuggest from "../../reusables/SelectMultipleWithSuggest";
import QuickSelectModal from "./QuickSelectModal";
import { getEWISMSRecipients, sendMessage, queueMessage, cancelQueuedMessage } from "../ajax";
import { getCurrentUser } from "../../sessions/auth";

function base_fn (props, func_specific) {
    const { 
        prepare_payload, enqueueSnackbar, modalStateHandler,
        closeSnackbar, snackBarActionFn, task_id
    } = props;

    const { 
        loading_message, success_message, error_message,
        ajaxFunction
    } = func_specific;
    
    const payload = prepare_payload();

    if (task_id) payload.task_id = task_id;

    const loading_snackbar = enqueueSnackbar(
        loading_message,
        {
            variant: "warning",
            persist: true
        }
    );

    modalStateHandler();

    ajaxFunction(payload, response => {
        closeSnackbar(loading_snackbar);
        const { message, status } = response;
        let temp = message;
        let variant;

        if (status) {
            variant = "success";
            temp = success_message;
        } else variant = "error";
        
        enqueueSnackbar(
            temp,
            {
                variant,
                autoHideDuration: 7000,
                action: snackBarActionFn
            }
        );
    }, () => {
        closeSnackbar(loading_snackbar);
        enqueueSnackbar(
            error_message,
            {
                variant: "error",
                autoHideDuration: 7000,
                action: snackBarActionFn
            }
        );
    });
}

function on_send_message_fn (props, from_ewi_modal = true) {
    let func_specific = { 
        loading_message: "Sending EWI SMS...",
        success_message: "EWI SMS sent!",
        error_message: "Error sending EWI SMS...",
        ajaxFunction: sendMessage
    };

    if (!from_ewi_modal) {
        func_specific = { 
            loading_message: "Sending message...",
            success_message: "Message sent!",
            error_message: "Error sending message...",
            ajaxFunction: sendMessage
        };
    }
    
    base_fn(props, func_specific);
}

function on_queue_fn (props, from_ewi_modal = true) {
    let func_specific = { 
        loading_message: "Queueing EWI SMS...",
        success_message: "EWI SMS sending successfully queued!",
        error_message: "Error queueing EWI SMS...",
        ajaxFunction: queueMessage
    };

    if (!from_ewi_modal) {
        func_specific = { 
            loading_message: "Queueing message...",
            success_message: "Message sending successfully queued!",
            error_message: "Error queueing message...",
            ajaxFunction: queueMessage
        };
    }
    
    base_fn(props, func_specific);
}

function on_cancel_queue_fn (props) {
    const func_specific = { 
        loading_message: "Canceling queued EWI SMS...",
        success_message: "Queued EWI SMS succesfully canceled!",
        error_message: "Error canceling queued EWI SMS...",
        ajaxFunction: cancelQueuedMessage
    };

    base_fn(props, func_specific);
}

function SendMessageForm (props) {
    const {
        textboxValue, disableQuickSelect,
        releaseId, modalStateHandler,
        recipientsList, fromEWIModal,
        // all the following is used only in EWISmsModal
        eventId, siteCode, updateSentStatusObj,
        prescribedReleaseTime, ewiModalTriggers
    } = props;
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();

    const current_user = getCurrentUser();

    const [recipients, setRecipients] = useState([]);
    const [options, setOptions] = useState([]);
    const [quick_select, setQuickSelect] = useState(false);
    const [composed_message, setComposedMessage] = useState(textboxValue);
    const [priority, setPriority] = useState("");
    const [ts_sending, setTsSending] = useState(null);
    const [sites, setSites] = useState([]);
    const [sendButtonFunction, setSendButtonFunction] = useState(null);

    const disable_select = typeof disableQuickSelect === "undefined" ? false : disableQuickSelect;
    const from_ewi_modal = typeof fromEWIModal === "undefined" ? false : fromEWIModal;

    const [is_loading_recipients, setIsLoadingRecipients] = useState(false);

    const snackBarActionFn = key => (<Button
        color="primary"
        onClick={() => { closeSnackbar(key); }}
    >
        Dismiss
    </Button>);

    const prepare_payload = () => {
        const recipient_list = [];
        let contact_person = "";

        if (from_ewi_modal) {
            recipients.forEach(row => {
                const { data } = row;
                const { mobile_numbers } = data;

                mobile_numbers.forEach(item => recipient_list.push({
                    mobile_id: item.mobile_number.mobile_id,
                    gsm_id: item.mobile_number.gsm_id,
                    data
                }));
            });
        } else {
            const temp = [];
            recipients.forEach(({ data: { mobile_id, gsm_id }, label }) => {
                recipient_list.push({
                    mobile_id, gsm_id
                });
                temp.push(label);
            });

            contact_person = temp.join(", ");
        }

        const formatted_message = `${composed_message} - ${current_user.nickname} from PHIVOLCS-DYNASLOPE`;

        let payload = {
            sms_msg: formatted_message,
            recipient_list,
            is_ewi: from_ewi_modal,
            priority: parseInt(priority, 10) || null,
            ts_sending: ts_sending ? ts_sending.format("YYYY-MM-DD HH:mm:ss") : null,
            user_id: current_user.user_id,
            tag_data: {
                site_list: sites.map(x => x.value),
                contact_person
            }
        };

        if (from_ewi_modal) {
            const { site_id, type } = updateSentStatusObj;

            payload = {
                ...payload,
                release_id: releaseId,
                event_id: eventId,
                site_id,
                alert_db_group: type,
                prescribed_release_time: prescribedReleaseTime,
                site_code: siteCode
            };
        }

        return payload;
    };

    useEffect(() => {
        if (typeof siteCode !== "undefined") {
            setIsLoadingRecipients(true);
            getEWISMSRecipients(siteCode, ewi_recipients_list => {
                const temp_ewi_recipients = [];
                const default_recipients = [];

                const {
                    type, public_alert_symbol: { alert_level },
                    highest_event_alert_level
                } = updateSentStatusObj;

                ewi_recipients_list.forEach(item => {
                    if (item.mobile_numbers.length > 0) {
                        const {
                            user_id, last_name, first_name,
                            organizations, ewi_recipient, ewi_restriction
                        } = item;
                        const { name, scope } = organizations[0].organization;
                        let org_name = name.toUpperCase();
                        
                        if (name === "lgu") {
                            let prefix;
                            if (scope === 1) prefix = "B";
                            if (scope === 2) prefix = "M";
                            if (scope === 3) prefix = "P";
                            org_name = prefix + org_name;
                        }
                    
                        const temp = {
                            label: `${last_name}, ${first_name} (${org_name})`,
                            value: user_id,
                            data: item
                        };

                        temp_ewi_recipients.push(temp);
                        if (ewi_recipient === 1) {
                            let to_push = true;
                            if (type === "extended") { 
                                // LEWC to MLGU
                                to_push = false;
                                const has_extended_routine_restriction = ewi_restriction !== null && ewi_restriction.alert_level === -1;
                                if ((name === "lewc" || (scope < 3 && name === "lgu")) && !has_extended_routine_restriction)
                                    to_push = true; 
                            } else if (ewi_restriction !== null) {
                                const { alert_level: limit_alert_level } = ewi_restriction;
                                if ((alert_level !== 0 && alert_level <= limit_alert_level) || 
                                    (alert_level === 0 && highest_event_alert_level <= limit_alert_level))
                                    to_push = false;
                            }
                            
                            if (to_push) default_recipients.push(temp);
                        }
                    }
                });
                setOptions(temp_ewi_recipients);
                setRecipients(default_recipients);
                setIsLoadingRecipients(false);
            });
        }
    }, [siteCode, updateSentStatusObj]);

    useEffect(() => {
        if (typeof recipientsList !== "undefined") {
            setIsLoadingRecipients(true);
            
            const temp = recipientsList.map(row => {
                const { label, mobile_id, org, sim_num } = row;
                let fin_label = label;
                if (org !== "") fin_label = `${label} (${org})`;
                fin_label += ` - ${sim_num}`;

                const chip_label = `${label} - ${sim_num}`;

                return {
                    label: fin_label,
                    chipLabel: chip_label,
                    value: mobile_id,
                    sim_num,
                    data: row
                };
            });

            setOptions(temp);
            setRecipients([]);
            setIsLoadingRecipients(false);
        }
    }, [recipientsList]);

    const temp_props = {
        prepare_payload, enqueueSnackbar, modalStateHandler,
        closeSnackbar, snackBarActionFn
    };

    useEffect(() => {
        let fn = on_send_message_fn;
        if (ts_sending) fn = on_queue_fn;
        setSendButtonFunction(() => fn);
    }, [ts_sending]);

    useEffect(() => {
        if (fromEWIModal && ewiModalTriggers.send.trigger) {
            ewiModalTriggers.send.revert();
            on_send_message_fn(temp_props);
        }
    }, [fromEWIModal, ewiModalTriggers, temp_props]);

    useEffect(() => {
        if (fromEWIModal && ewiModalTriggers.queue.trigger) {
            ewiModalTriggers.queue.revert();
            on_queue_fn(temp_props);
        }
    }, [fromEWIModal, ewiModalTriggers, temp_props]);

    useEffect(() => {
        if (fromEWIModal && ewiModalTriggers.cancel_queue.trigger) {
            ewiModalTriggers.cancel_queue.revert();
            temp_props.task_id = ewiModalTriggers.cancel_queue.task_id;
            on_cancel_queue_fn(temp_props);
        }
    }, [fromEWIModal, ewiModalTriggers, temp_props]);

    useEffect(() => {
        if (fromEWIModal) {
            ewiModalTriggers.disable_send_queue_buttons(recipients === null || recipients.length === 0);
        }
    }, [fromEWIModal, recipients, ewiModalTriggers]);

    const handle_message_fn = event => setComposedMessage(event.target.value);

    const is_recipients_empty = recipients === null || recipients.length === 0;
    const is_message_empty = composed_message === "" || typeof composed_message === "undefined";

    return (
        <Fragment>
            <Grid container justify="space-around" alignItems="flex-end">
                {
                    is_loading_recipients && (
                        <Grid item xs={1}>
                            <CircularProgress size={20} />
                        </Grid>
                    )
                }

                <Grid item xs>
                    <SelectMultipleWithSuggest
                        label="Recipients"
                        options={options}
                        value={recipients}
                        changeHandler={value => setRecipients(value)}
                        placeholder="Select recipients"
                        renderDropdownIndicator
                        openMenuOnClick
                        isMulti
                    />
                </Grid>

                {
                    !disable_select && (
                        <Grid item style={{ textAlign: "right" }}>
                            <IconButton
                                aria-label="Quick select option"
                                onClick={value => setQuickSelect(true)}
                            >
                                <AddBox />
                            </IconButton>
                        </Grid>
                    )
                }
            </Grid>
            
            <QuickSelectModal
                isOpen={quick_select}
                closeHandler={value => setQuickSelect(false)}
                setRecipients={setRecipients}
            />
                
            <div style={{ marginTop: 16 }}>
                <MessageInputTextbox
                    limitRows={false}
                    value={composed_message}
                    disableSend={is_recipients_empty || is_message_empty}
                    sendButtonClickHandler={() => sendButtonFunction(temp_props, false)}
                    messageChangeHandler={handle_message_fn}
                    setComposedMessage={setComposedMessage}
                    disableTemplateLoader={from_ewi_modal}
                    fromEWIModal={from_ewi_modal}
                    setPriority={setPriority}
                    setTsSending={setTsSending}
                    priority={priority}
                    tsSending={ts_sending}
                    sites={sites}
                    setSites={setSites}
                />
            </div>
                
        </Fragment>
    );
}

export default SendMessageForm;
