import React, { Fragment } from "react";
import {
    Dialog, DialogTitle, DialogContent,
    DialogContentText, DialogActions,
    Button, withMobileDialog, IconButton,
    Grid, CircularProgress, makeStyles
} from "@material-ui/core";
import { Close } from "@material-ui/icons";
import { useSnackbar } from "notistack";

import GeneralStyles from "../../../GeneralStyles";
import { getCurrentUser } from "../../sessions/auth";
import MessageInputTextbox from "../../communication/chatterbox/MessageInputTextbox";
import {
    sendRoutineEwiMessage, queueRoutineEwiMessage,
    cancelQueuedRoutineEwiMessage
} from "../../communication/ajax";
import { SlideTransition, FadeTransition } from "../../reusables/TransitionList";

const useStyles = makeStyles(theme => GeneralStyles(theme));

function SendRoutineMessageForm (props) {
    const {
        is_loading_recipients, textboxValue,
        sendHandler
    } = props;

    return (
        <Fragment>
            <Grid container justify="space-around" alignItems="flex-end">
                {
                    is_loading_recipients && (
                        <Grid item xs={1}>
                            <CircularProgress size={20} />
                        </Grid>
                    )
                }
            </Grid>

            <div style={{ marginTop: 16 }}>
                <MessageInputTextbox
                    limitRows={false}
                    value={textboxValue}
                    disableSend={false}
                    sendButtonClickHandler={sendHandler}
                    messageChangeHandler={() => console.log("tried msg change")}
                    disableTemplateLoader
                    fromEWIModal
                />
            </div>
                
        </Fragment>
    );
}

function SendRoutineEwiSmsModal (props) {
    const {
        fullScreen, modalStateHandler,
        modalState, textboxValue, routineData
    } = props;
    const classes = useStyles();

    const {
        released_sites, prescribed_release_time,
        not_yet_sending_time, is_sms_sent, is_sms_queued
    } = routineData;

    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    const snackBarActionFn = key => {
        return (<Button
            color="primary"
            onClick={() => { closeSnackbar(key); }}
        >
            Dismiss
        </Button>);
    };

    const baseFn = func_specific => {
        const {
            loading_message, success_message, error_message,
            ajaxFunction
        } = func_specific;
        const { user_id, nickname } = getCurrentUser();
        const input = {
            alert_db_group: "routine",
            site_list: released_sites,
            prescribed_release_time,
            user_id,
            nickname 
        };
        if (is_sms_queued) {
            input.task_id = is_sms_queued.remarks;
        }

        const loading_snackbar = enqueueSnackbar(
            loading_message,
            { variant: "warning", persist: true }
        );
        modalStateHandler();

        ajaxFunction(input, response => {
            closeSnackbar(loading_snackbar);
            console.log(response);

            let message = success_message;
            let options = {
                variant: "success",
                autoHideDuration: 7000,
                action: snackBarActionFn
            };
            if (!response.status) {
                const temp = response.site_ids.map(x => x.toUpperCase());
                message = `Some problems occurred sending EWI SMS to ${temp.join(", ")}`;
                options = {
                    variant: "error",
                    persist: true,
                    action: snackBarActionFn
                };
            }

            enqueueSnackbar(message, options);
        }, error_response => {
            console.log("error_response", error_response);
            enqueueSnackbar(
                error_message,
                {
                    variant: "error",
                    autoHideDuration: 7000,
                    action: snackBarActionFn
                }
            );
        });
    };
    
    const sendHandler = () => {
        const func_specific = { 
            loading_message: "Sending routine EWI SMS...",
            success_message: "Routine EWI SMS sent!",
            error_message: "Error sending routine EWI SMS...",
            ajaxFunction: sendRoutineEwiMessage
        };

        baseFn(func_specific);
    };

    const queueHandler = () => {
        const func_specific = { 
            loading_message: "Queueing routine EWI SMS...",
            success_message: "Routine EWI SMS sucessfully queued!",
            error_message: "Error queueing routine EWI SMS...",
            ajaxFunction: queueRoutineEwiMessage
        };

        baseFn(func_specific);
    };

    const cancelQueueHandler = () => {
        const func_specific = { 
            loading_message: "Canceling queued routine EWI SMS...",
            success_message: "Queued routine EWI SMS succesfully canceled!",
            error_message: "Error canceling queued routine EWI SMS...",
            ajaxFunction: cancelQueuedRoutineEwiMessage
        };

        baseFn(func_specific);
    };
    
    return (
        <Dialog
            fullWidth
            fullScreen={fullScreen}
            open={modalState}
            aria-labelledby="form-dialog-title"
            TransitionComponent={fullScreen ? SlideTransition : FadeTransition}
                    
        >
            <DialogTitle id="form-dialog-title">
                <div style={{ display: "flex", alignItems: "center", justifyContent: "space-between" }}>
                    <span>Send Routine Early Warning Information</span>
                    <IconButton 
                        color="inherit" 
                        onClick={modalStateHandler}
                        aria-label="Close"
                        style={{ padding: 0 }}
                    >
                        <Close />
                    </IconButton>
                </div>
            </DialogTitle>
            <DialogContent style={{ overflowY: "hidden" }}>
                <DialogContentText>
                    Early warning information recipients for this site is already pre-loaded. 
                    Recipients are LEWC, Barangay, and Municipal LGUs per site. If you need to add a new recipient, 
                    add a new community contact via chatterbox.
                </DialogContentText>

                <DialogContentText className={classes.dyna_error}>
                    NOTE: Ensure first that alerts for ALL sites under routine monitoring has been released before sending 
                    routine EWI SMS. Sending routine EWI in multiple batches will result in message duplication on our stakeholders.
                </DialogContentText>
                        
                <div style={{ marginTop: 20 }}>
                    <SendRoutineMessageForm
                        isMobile={fullScreen}
                        textboxValue={textboxValue}
                        disableQuickSelect
                        sendHandler={sendHandler}
                        fromEWIModal
                        updateSentStatusObj=""
                        modalStateHandler={modalStateHandler}
                    />
                </div>
            </DialogContent>
            <DialogActions>
                {
                    not_yet_sending_time && !is_sms_queued && (
                        <Button onClick={queueHandler} color="primary">
                            Queue
                        </Button>
                    )
                }
                {
                    not_yet_sending_time && is_sms_queued && (
                        <Button color="primary" onClick={cancelQueueHandler}>
                            Cancel Queue
                        </Button>
                    )
                }
                {
                    !not_yet_sending_time && <Button onClick={sendHandler} color="primary">
                        { is_sms_sent ? "Send Again" : "Send" }
                    </Button>
                }
                <Button onClick={modalStateHandler} color="primary">
                    Cancel
                </Button>
            </DialogActions>
        </Dialog>
    );
}

export default withMobileDialog()(SendRoutineEwiSmsModal);
